import { CSSProperties, useMemo } from "react";
import styles from "./SubscriptionPlanTile.module.scss";
import { formatToCurrency } from "../../utils";
import { SubscriptionPlan } from "./subscriptionPlansSlice";
import widgets from "../widgets/widgets";

interface Props extends SubscriptionPlan {
  className?: string;
  style?: CSSProperties;
  status?: "current" | "requested";
  clickHandler?: () => void;
}

export const SubscriptionPlanTile = ({
  className = "",
  style,
  name,
  prices,
  pricesBilledAnually,
  searchesLimit,
  dashboardsLimit,
  marketsLimit,
  company,
  availableWidgets,
  status,
  clickHandler,
}: Props) => {
  const features = [
    {
      label: "Monthly searches",
      value: searchesLimit,
    },
    {
      label: "Brand dashboards",
      value: dashboardsLimit,
    },
    {
      label: "Markets",
      value: marketsLimit,
    },
    {
      label: "Company members/admins",
      value: `${company.membersLimit}/${company.adminsLimit}`,
    },
  ];

  const widgetNames = useMemo(() => {
    return availableWidgets.reduce<string[]>((acc, widget) => {
      const widgetName = widgets.entities[widget]?.name;

      if (widgetName) {
        acc.push(widgetName);
      }

      return acc;
    }, []);
  }, [availableWidgets]);

  return (
    <div
      className={`${styles.subscriptionPlanTile} ${className}`}
      style={style}
    >
      <div className={styles.name} title={name}>
        {name}
      </div>
      <div
        className={styles.price}
        title={`${formatToCurrency(prices.gbp, "gbp")} (${formatToCurrency(
          prices.sek,
          "sek"
        )}) / per month`}
      >
        <span>
          {formatToCurrency(prices.gbp, "gbp")}
          <span>{formatToCurrency(prices.sek, "sek")}</span>
        </span>{" "}
        <sub>/per month</sub>
      </div>
      <div
        className={styles.priceBilledAnually}
        title={`${formatToCurrency(
          pricesBilledAnually.gbp,
          "gbp"
        )} (${formatToCurrency(
          pricesBilledAnually.sek,
          "sek"
        )}) / per month billed annually`}
      >
        <span>
          {formatToCurrency(pricesBilledAnually.gbp, "gbp")}
          <span>{formatToCurrency(pricesBilledAnually.sek, "sek")}</span>
        </span>{" "}
        <sub>/per month billed annually</sub>
      </div>
      {features.map(({ label, value }, i) => (
        <div className={styles.feature} key={i}>
          <div>{label}</div>
          <div>{value}</div>
        </div>
      ))}
      <div className={styles.feature}>
        <div>Included widgets</div>
      </div>
      <div className={styles.widgets}>
        {widgetNames.map((widget) => (
          <div className={styles.widget} title={widget} key={widget}>
            - {widget}
          </div>
        ))}
      </div>
      <div className={styles.bottom}>
        {status ? (
          <div>
            {" "}
            <span style={{ textTransform: "capitalize" }}>{status}</span> plan
          </div>
        ) : (
          clickHandler && (
            <button className={styles.button} onClick={clickHandler}>
              Choose this plan
            </button>
          )
        )}
      </div>
    </div>
  );
};
