import React, { CSSProperties, useState } from "react";
import styles from "./CompanyMemberDropdown.module.scss";
import { Triangle } from "../../../../assets/icons";
import { RED, GOLD, GREEN } from "../../../../data/colors";

interface Props {
  className?: string;
  style?: CSSProperties;
  fullName?: string;
  isAdmin?: boolean;
  isCurrentUser?: boolean;
  email?: string;
  searchNames?: string[];
  clickDeleteHandler?: () => void;
  clickDemoteHandler?: () => void;
  clickPromoteHandler?: () => void;
}

export const CompanyMemberDropdown = ({
  className = "",
  style,
  fullName,
  isAdmin,
  isCurrentUser,
  email,
  searchNames = [],
  clickDeleteHandler,
  clickDemoteHandler,
  clickPromoteHandler,
}: Props) => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <div
      className={`${styles.companyMemberDropdown} ${
        isOpen ? styles.companyMemberDropdownOpen : ""
      }  ${className}`}
      style={style}
    >
      <div
        className={styles.opener}
        onClick={() => setIsOpen((prevIsOpen) => !prevIsOpen)}
      >
        <div>
          {fullName} {isAdmin && "(admin)"} {isCurrentUser && " - me"}
        </div>
        <div>
          <Triangle
            style={{ ...(!isOpen && { transform: "rotate(180deg)" }) }}
            size={8}
          />
        </div>
      </div>
      {isOpen && (
        <div className={styles.popup}>
          <div className={styles.divider} />
          {email && <div>Email: {email}</div>}
          <div>
            Searches:{" "}
            {searchNames.length ? (
              <ul className={styles.list}>
                {searchNames.map((name, i) => (
                  <li key={i}>{name}</li>
                ))}
              </ul>
            ) : (
              "-"
            )}
          </div>
          {(clickDeleteHandler ||
            clickDemoteHandler ||
            clickPromoteHandler) && (
            <div className={styles.buttonsWrapper}>
              {clickDeleteHandler && (
                <button
                  className={styles.button}
                  style={{ borderColor: RED }}
                  onClick={clickDeleteHandler}
                >
                  Delete
                </button>
              )}
              {clickDemoteHandler && (
                <button
                  className={styles.button}
                  style={{ marginLeft: "auto", borderColor: GOLD }}
                  onClick={clickDemoteHandler}
                >
                  Demote
                </button>
              )}
              {clickPromoteHandler && (
                <button
                  className={styles.button}
                  style={{ marginLeft: "auto", borderColor: GREEN }}
                  onClick={clickPromoteHandler}
                >
                  Promote
                </button>
              )}
            </div>
          )}
        </div>
      )}
    </div>
  );
};
