import { useContext, useState, useMemo } from "react";
import styles from "./Company.module.scss";
import context from "../../../context";
import { connect, useSelector } from "react-redux";
import { selectCompanySubscriptionPlan } from "../../../features/subscriptionPlans/subscriptionPlansSlice";
import { useHistory } from "react-router-dom";
import { Input, BasicModal } from "../../../components";
import { CompanyMemberDropdown } from "./CompanyMemberDropdown/CompanyMemberDropdown";
import { SubscriptionPlanTile } from "../../../features/subscriptionPlans/SubscriptionPlanTile";
import { notify } from "react-notify-toast";
import * as actions from "../../../store/actions";

interface Props {
  userId: Store.User["id"];
  company: Store.Company;
  searches: Store.Searches;
  updateCompanyData: typeof actions.updateCompanyData;
  deleteCompanyMember: typeof actions.deleteCompanyMember;
  toggleCompanyMemberStatus: typeof actions.toggleCompanyMemberStatus;
}

const Company = ({
  userId,
  company,
  searches,
  updateCompanyData,
  deleteCompanyMember,
  toggleCompanyMemberStatus,
}: Props) => {
  const history = useHistory();

  const invitationLink =
    window.location.origin + "/auth/signup?company=" + company.id;

  const companySubscriptionPlan = useSelector(selectCompanySubscriptionPlan);

  const { setModalElement, setIsGlobalPreloaderShown } = useContext(context);

  const [name, setName] = useState(company.name);

  const isMemberPromotionAvailable = useMemo(
    () =>
      (companySubscriptionPlan?.company.adminsLimit || 1) >
      company.members.filter(({ isAdmin }) => isAdmin).length,
    [companySubscriptionPlan, company.members]
  );

  async function handleChangeInput(payload: { name?: string }) {
    try {
      await updateCompanyData(payload);
    } catch (error) {
      console.log(error);
      notify.show("Oops, something went wrong", "error");
    }
  }

  async function handleClickInvitationButton() {
    navigator.clipboard.writeText(invitationLink);
    notify.show("Link has been copied to clipboard", "success");
  }

  async function handleClickDeleteMember(
    member: Store.Company["members"][number]
  ) {
    const memberSearchIds = searches
      .filter(({ owner }) => owner.id === member.id)
      .map(({ id }) => id);

    setModalElement(
      <BasicModal
        redButton={{ onClicked: () => setModalElement(null) }}
        greenButton={{
          onClicked: async () => {
            setIsGlobalPreloaderShown(true);
            try {
              await deleteCompanyMember(memberSearchIds, member.id);
              setModalElement(null);
              notify.show("Member has been deleted", "success");
            } catch (error) {
              console.dir(error);
              notify.show("Oops, something went wrong", "error");
            }
            setIsGlobalPreloaderShown(false);
          },
        }}
      >
        Do you really want to remove {member.fullName}?
        {memberSearchIds.length
          ? " The searches which belong to this user will be transferred to your account."
          : ""}
      </BasicModal>
    );
  }

  async function handleClickChangeMemberStatus(
    member: Store.Company["members"][number]
  ) {
    setModalElement(
      <BasicModal
        redButton={{ onClicked: () => setModalElement(null) }}
        greenButton={{
          onClicked: async () => {
            setIsGlobalPreloaderShown(true);
            try {
              await toggleCompanyMemberStatus(member);
              setModalElement(null);
              notify.show("Member's status has been updated", "success");
            } catch (error) {
              console.dir(error);
              notify.show("Oops, something went wrong", "error");
            }
            setIsGlobalPreloaderShown(false);
          },
        }}
      >
        Do you really want to {member.isAdmin ? "demote" : "promote"}{" "}
        {member.fullName}? This member will {member.isAdmin ? "lose" : "get"}{" "}
        the admin status.
      </BasicModal>
    );
  }

  return (
    <div className={styles.company}>
      <div className={styles.title}>Your company</div>
      <div className={styles.content}>
        <div className={styles.section}>
          <div className={styles.inputWrapper}>
            <div className={styles.label}>Name</div>
            <Input
              className={styles.input}
              value={name}
              changeHandler={(value) => {
                setName(value);
                handleChangeInput({ name: value });
              }}
            />
          </div>
          <div className={styles.inputWrapper}>
            <div className={styles.label}>Invitation link</div>
            <Input className={styles.input} value={invitationLink} isReadOnly />
            <button
              className={styles.button}
              onClick={handleClickInvitationButton}
            >
              Copy link
            </button>
          </div>
        </div>
        <div className={styles.section}>
          <div className={styles.label}>
            Members{" "}
            {!!companySubscriptionPlan &&
              `(${company.members.length}/${companySubscriptionPlan.company.membersLimit})`}
          </div>
          <div>
            {company.members.map((member, i) => (
              <CompanyMemberDropdown
                className={styles.companyMemberDropdown}
                fullName={member.fullName}
                isAdmin={member.isAdmin}
                isCurrentUser={member.id === userId}
                email={member.email}
                searchNames={searches
                  .filter(({ owner }) => owner.id === member.id)
                  .map(({ name }) => name)}
                clickDeleteHandler={
                  member.id !== userId
                    ? () => handleClickDeleteMember(member)
                    : undefined
                }
                clickDemoteHandler={
                  member.id !== userId && member.isAdmin
                    ? () => handleClickChangeMemberStatus(member)
                    : undefined
                }
                clickPromoteHandler={
                  isMemberPromotionAvailable && !member.isAdmin
                    ? () => handleClickChangeMemberStatus(member)
                    : undefined
                }
                key={i}
              />
            ))}
          </div>
        </div>
        <div className={styles.section}>
          {!!companySubscriptionPlan && (
            <>
              <div className={styles.label}>Subscription plan</div>
              <SubscriptionPlanTile {...companySubscriptionPlan} />
              <button
                className={styles.button}
                onClick={() => history.push("/pricing")}
              >
                Check available plans
              </button>
            </>
          )}
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = ({ user, company, searches }: Store.State) => ({
  userId: user.id,
  company,
  searches,
});

export default connect(mapStateToProps, {
  updateCompanyData: actions.updateCompanyData,
  deleteCompanyMember: actions.deleteCompanyMember,
  toggleCompanyMemberStatus: actions.toggleCompanyMemberStatus,
})(Company);
